export default () => {
    $(document).off("click", ".menu li").on("click", ".menu li", function () {
        // 
        $(this).find("button").attr("disabled", "disabled").delay(100).animate({ disabled: '' });
        //  ztoAjax 
        var index = $(this).index();
        //   630644632616_0 
        var bill = $(this).find("button").attr("data-bill");
        // 
        if ($(this).find("button").html() === "" || $(this).find("button").html() === "") {
            return
        }
        if (!$(this).hasClass("curr")) {
            // 
            var url = $(this).find("button").attr("data-url");
            var id = $(this).find("button").attr("data-id");
            var text = $(this).find("button").text().trim();
            var queryParms = getUrlParmas(url);
            var currentButton = this;
            // 
            const billQueryPreauthFn = () => {
                return new Promise((resolve, reject) => {
                    let ticket = ''
                    const doIt = () => {
                        ztosec.billQueryPreauth({
                            bill: queryParms.id,
                            billType: queryParms.type
                        }, function (params) {
                            ticket = params.ticket
                        })
                        setTimeout(() => {
                            if (ticket) {
                                resolve(ticket)
                            } else {
                                doIt()
                            }
                        }, 1000)
                    }
                    doIt()
                })
            }
            billQueryPreauthFn()
                //  
                .then(ticket => {
                    ztoAjax({
                        url: url + "&queryTicket=" + ticket,
                        type: "get",
                        data: "",
                        index: index,
                        bill: bill,
                        id: id,
                        text: text
                    });
                    $(currentButton).addClass("curr");
                })
            // 
            // ztosec.billQueryPreauth({
            //     bill: queryParms.id,
            //     billType: queryParms.type
            // }, function (params) {
            //         ztoAjax({
            //             url: url + "&queryTicket=" + params.ticket,
            //             type: "get",
            //             data: "",
            //             index: index,
            //             bill: bill,
            //             id: id,
            //             text: text
            //         });
            //         $(currentButton).addClass("curr");
            //     }
            // )
        } else {
            $(this).removeClass("curr");
            var id = $(this).find("button").attr("data-id");
            if ($(this).find("button").html() != "") {
                $("." + id).remove();
            } else {
                $("." + id).removeClass("curr");
            }
        }
    })
}